* {
  box-sizing: border-box;
}

html {
  --text-color: #00acdc;
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
  font-size: 12px;
  font-weight: 500;
}

.header {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo {
  margin-left: 50px;
  text-decoration: none;
  display: block;
}

.nav {
  margin-right: 50px;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover {
  color: var(--text-color);
}

.nav__item {
  margin-right: 20px;
}

.nav__item:last-of-type {
  margin-right: 0;
}

.is-active {
  color: var(--text-color);
}

.is-active:after {
  content: "";
  background-color: var(--text-color);
  border-radius: 8px;
  width: 37px;
  height: 4px;
  position: absolute;
  top: 56px;
}
